html, body {
  margin: 0;
  height: 100%;
  overflow-x: hidden; /* Prevent horizontal scroll */
}

body {
  text-align: center;
  background-image: url('https://c0.wallpaperflare.com/preview/809/972/847/silhouette-of-trees-under-white-and-blue-sky.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: repeat-y;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px; /* Reduced gap for smaller screens */
  color: white;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 20px; /* Padding for better spacing */
}

.mainhead {
  font-family: Arial, Helvetica, sans-serif;
  font-style: oblique;
  color: whitesmoke;
  font-size: 2em; /* Responsive font size */
}

.search_city {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px; /* Reduced gap */
  background-color: rgba(255, 255, 255, 0.2);
  width: 90%; /* Use percentage width for responsiveness */
  max-width: 700px;
  border-radius: 10px;
  padding: 20px; /* Added padding */
}

.search {
  background-color: rgb(8, 5, 5);
  color: whitesmoke;
  width: 100%; /* Make the search bar full width */
  height: 40px; /* Increased height for better touch targets */
  border-radius: 10px;
  border: 2px solid blueviolet; /* Changed to solid border for visibility */
}

label {
  font-size: 1.2em; /* Responsive font size */
}

button {
  width: 100px; /* Make button full width */
  height: 40px; /* Increased height for better touch targets */
  background-color: black;
  color: aqua;
  border-radius: 10px;
}

.Weather {
  width: 90%; /* Use percentage width */
  background-color: rgba(255, 255, 255, 0.2);
  margin: 20px 0; /* Reduced margin for better spacing */
  padding: 20px; /* Added padding for better spacing */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  .mainhead {
    font-size: 1.8em; /* Smaller font size for mobile */
  }

  .search_city {
    width: 95%; /* Adjust width for smaller screens */
    padding: 10px; /* Reduced padding */
  }

  .search{
    height: 50px; /* Make buttons larger for easier tapping */
  }
  button{
  width: 100px;
  }

  label {
    font-size: 1em; /* Smaller font size */
  }
}

@media (max-width: 480px) {
  .mainhead {
    font-size: 1.5em; /* Even smaller font size for very small screens */
  }

  .search_city {
    padding: 8px; /* Further reduced padding */
  }

  .search {
    height: 45px; /* Smaller height for very small screens */
  }
  button{
    width: 100px;
  }

  label {
    font-size: 0.9em; /* Smaller font size */
  }
}
